}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  background-color: #68b738;
  color: #ffffff;
  font-size: 14px;
  text-transform: uppercase;
  padding: 11px 0;
  text-align: center;
}
.button--additional {
  font-size: 16px;
}
.button--green:hover {
  background-color: #5eaa2f;
}
.button--green:focus {
  background-color: #5eaa2f;
}
.button--green:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button--silver:hover {
  background-color: #ebebeb;
}
.button--silver:focus {
  background-color: #ebebeb;
}
.button--silver:active {
  background-color: #ebebeb;
  color: rgba(0, 0, 0, 0.3);
}
.page-header {
  position: relative;
  border-bottom: 1px solid #ededed;
}
.page-header--promo::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  width: 100%;
  background: url("../img/index-background-mobile@1x.jpg") no-repeat center center;
  background-color: #68b738;
  background-size: cover;
  min-height: 305px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-header--promo::after {
    background-image: url("../img/index-background-mobile@2x.jpg") no-repeat center center;
  }
}
@media (min-width: 768px) {
  .page-header {
    border-bottom: none;
  }
  .page-header--promo::after {
    display: none;
  }
}
@media (min-width: 1300px) {
  .page-header--promo::after {
    display: block;
    width: 47%;
    min-height: 696px;
    top: 0;
    background: url("../img/index-background-desktop@1x.jpg") no-repeat center center;
    background-size: cover;
  }
}
@media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
  .page-header--promo::after {
    background-image: url("../img/index-background-desktop@2x.jpg") no-repeat center center;
  }
}
.main-nav {
  display: flex;
  flex-wrap: wrap;
}
.main-nav__logo {
  margin-top: 11px;
  margin-bottom: 13px;
  width: 191px;
  display: flex;
  justify-content: space-between;
}
.main-nav__logo:hover,
.main-nav__logo:focus {
  opacity: 0.8;
}
