  margin-top: 13px;
}
.main-nav__toggle-close {
  background: url("../img/icon-close.svg") no-repeat center center;
  width: 18px;
  height: 18px;
  position: absolute;
  right: 20px;
  top: 23px;
  display: none;
  cursor: pointer;
}
.main-nav__toggle {
  border: none;
  background: url("../img/icon-mobile-menu.svg") no-repeat 0 0;
  background-color: transparent;
  width: 24px;
  height: 18px;
  margin-top: 23px;
  position: absolute;
  right: 20px;
  overflow: hidden;
  cursor: pointer;
}
.main-nav__toggle:focus {
  outline: none;
}
.main-nav__wrapper {
  width: 100%;
  background: white;
  z-index: 1000;
  display: none;
}
.main-nav--closed .main-nav__wrapper {
  width: 100%;
  z-index: 1000;
  display: none;
}
.main-nav--opened .main-nav__wrapper {
  width: 100%;
  z-index: 1000;
  display: block;
}
.main-nav--opened .main-nav__toggle-close {
  display: block;
}
.main-nav--opened .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  display: block;
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 20px;
