.social {
  border-bottom: 1px solid silver;
  border-top: 1px solid silver;
}

.social__list {
  list-style: none;
  padding: 20px 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  position: relative;
  min-width: 220px;
}

.social__item {
  padding: 0 18px;
  width: 22px;
}

.social__vkontakte {
  &--small {
    width: 22px;
    height: 12px;
    fill: #666666;
  }

  &--big {
    display: none;
    fill: #666666;
  }
}

.social__instagram {
  &--small {
    width: 16px;
    height: 16px;
    fill: #666666;
  }

  &--big {
    display: none;
    fill: #666666;
  }
}

.social__facebook {
  &--small {
    width: 10px;
    height: 19px;
    fill: #666666;
  }

  &--big {
    display: none;
    fill: #666666;
  }
}

.social__link {
  display: block;
}

.social__link:hover .social__vkontakte,
.social__link:focus .social__vkontakte {
  fill: @main-color;
}

.social__link:active .social__vkontakte {
  opacity: 0.3;
}

.social__link:hover .social__instagram,
.social__link:focus .social__instagram {
  fill: @main-color;
}

.social__link:active .social__instagram {
  opacity: 0.3;
}

.social__link:hover .social__facebook,
.social__link:focus .social__facebook {
  fill: @main-color;
}

.social__link:active .social__facebook {
  opacity: 0.3;
}

@media (min-width: @tablet-width) {
  .social {
    border: none;
    margin: 0;
  }

  .social__list {
    margin: 0;
    padding: 0;
    width: 324px;
    flex-wrap: wrap;
  }

  .social__item {
    padding: 0 14px;
    width: auto;
  }

  .social__vkontakte {
    &--small {
      display: none;
    }

    &--big {
      display: block;
      width: 30px;
      height: 16px;
    }
  }

  .social__instagram {
    &--small {
      display: none;
    }

    &--big {
      display: block;
      width: 23px;
      height: 23px;
    }
  }

  .social__facebook {
    &--small {
      display: none;
    }

    &--big {
      display: block;
      width: 14px;
      height: 28px;
    }
  }
}

@media (min-width: @desktop-width) {
  .social {
    width: 730px;
    padding: 0;
    margin: 0;
  }

  .social__list {
    width: auto;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
  }
}
